.contact__container {
    margin-top: 2rem;
    row-gap: 3rem;
    padding-bottom: 3rem;
}
.contact__title {
    text-align: center;
    font-size: var(--h3-font-size);
    margin-bottom: 1.5rem;
}
.contact__info {
    display: grid;
    gap: 1rem;
}
.contact__card {
    background-color: var(--container-color);
    padding: 1rem;
    border-radius: 0.75rem;
    text-align: center;
}
.contact__card__icon {
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: 0.25rem;
}
.contact__card__title,
.contact__card__data {
    font-size: var(--small-font-size);
}
.contact__card__title {
    font-weight: var(--font-medium);
}
.contact__card__data {
    display: block;
    margin-bottom: 0.75rem;
}
.contact__button {
    color: var(--first-color);
    font-size: var(--small-font-size);
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.25rem;
}
.contact__button:hover .contact__button__icon {
    transform: translateX(0.25rem);
}
.contact__button__icon {
    font-size: 1.2rem;
    transition: 0.4s;
}
.contact__form__control {
    position: relative;
    margin-bottom: 2rem;
    height: 4rem;
}
.contact__form__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid var(--text-color-light);
    background: none;
    outline: none;
    padding: 1.5rem;
    border-radius: 0.75rem;
    z-index: 1;
}
.contact__form__label {
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    padding: 0.25rem;
    background-color: var(--body-color);
    z-index: 10;
}
.contact__form__area {
    height: 11rem;
}
.contact__form__area textarea {
    resize: none;
}
